        "dish": "Pozole Pork",
        "description": "Slow Cooker Pozole (Traditional Mexican Hominy Stew)    1. Prepare the Red chili Sauce (For Pozole Rojo Only)    Remove stems and seeds from guajillo and ancho chilis.    Soak chilis in 2 cups hot water for 15 minutes until soft.    Blend softened chilis with garlic, salt, and the soaking water until smooth.    Strain sauce through a sieve to remove solids. Set aside.    2. Slow Cook the Meat & Broth    Place pork shoulder (or chicken), broth, hominy, onion, garlic, bay leaves, oregano, cumin, salt, and pepper in the slow cooker.    If making Pozole Rojo, add the red chili sauce now. If making Pozole Blanco, skip the chili sauce.    Cover and cook on LOW for 8 hours (or HIGH for 4-5 hours).    3. Shred the Meat & Serve    Remove the pork from the slow cooker, shred it with forks, and discard any excess fat.    Return shredded meat to the slow cooker and stir well. Adjust seasoning if needed.    Serve hot with cabbage, radishes, onions, cilantro, lime wedges, and tostadas on the side!    Variations:     Pozole Verde (Green Pozole): Blend roasted tomatillos, jalapeos, cilantro, and pumpkin seeds into a sauce instead of using red chilis.     Chicken Pozole: Use chicken thighs instead of pork and reduce the cooking time to 6 hours on LOW or 3 hours on HIGH.     Spicy Pozole: Add extra chili de rbol or serve with crushed red pepper.",
